.roundComplete {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    margin: auto;
    align-items: center;
    justify-content: center;
}

.completeButton {
    border: none;
    font-size: 4rem;
    background-color: var(--purple);
    color: white;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    border-radius: 2rem;
    height: 6rem;
    width: 10rem;
}